import uniIcon from "@/components/uni-icon/uni-icon.vue"
import uniPopup from "@/components/uni-popup/uni-popup.vue"
export default {
	components: {
		uniIcon,
		uniPopup
	},
	data() {
		return {
			height: 0,
			caseId: "", //100001
			info:false,
		}
	},
	onLoad(options) {
		if(!!options.id){
			this.caseId = options.id;
		}else{
			this.caseId = "";
		}
		this.height = uni.getSystemInfoSync().windowHeight;
		this.getInfo();
	},
	onShow() {
		if(!uni.getStorageSync('opid')){
			this.checkLogin();
		}
	},
	methods: {
		getInfo(){
			let _this = this;
			uni.getSetting({
				success(res) {
					if(res.authSetting["scope.userInfo"]){
						_this.info = true;
					}else{
						_this.info = false;
					}
				}
			});
		},
		userAuthorize(e){
			uni.getUserInfo({
				provider: 'weixin',
				success: (infoRes) => {
					let data = JSON.parse(infoRes.rawData);
					this.info = true;
					this.checkTel();
				},
				fail: () => {
					uni.showModal({
						title: '警告',
						content: '您点击了拒绝授权，将无法使用小程序，请授权之后再使用!',
						showCancel: false,
						confirmText: "返回授权",
						success: function (res) {
							if (res.confirm) {
								console.log('用户点击确定');
							}
						}
					});
				}
			});
		},
		checkTel(){
			uni.login({
				provider: 'weixin',
				success: function(loginRes) {
					uni.request({
						url: 'http://xyxh.fryxy.cn/testDeBug/login/weChatAuthorized',
						method: 'POST',
						data: {
							code: loginRes.code,
							loginType: 'user'
						},
						header: {
							'content-Type': 'application/x-www-form-urlencoded',
						},
						success: (res) => {
							if(res.data.status == 505){
								uni.setStorageSync('sessionKey',res.data.data);
								uni.navigateTo({url: '../tel/tel'});
							}else if(res.data.status == 200){
								uni.setStorageSync('opid',res.data.data);
							}
						},
						fail: (res) => {
							uni.showToast({
								title: res.data.msg,
								icon: 'none',
								duration: 2000
							});
						}
					});
				}
			});
		},
		server(){
			uni.makePhoneCall({
				phoneNumber: '0571-85873569'
			});
		},
		showPopup(){
			this.$refs.coupon.open();
		},
		hidePopup(){
			this.$refs.coupon.close();
		},
		go(url){
			uni.navigateTo({url: url});
		},
		lost(){
			uni.navigateTo({url: '../lost/lost'});
		},
		storageBag(){
			if(uni.getStorageSync('opid')){
				let id = this.caseId;
				if(!!id){
					uni.navigateTo({url: '../storage/storage?id='+id});
				}else{
					uni.scanCode({
						onlyFromCamera: true,
						success: function (res) {
							// console.log('条码类型：' + res.scanType);
							console.log(res.result);
							let id = res.result.split('id%3D')[1].split('#')[0];
							uni.navigateTo({url: '../storage/storage?id='+id});
						}
					});
				}
			}else{
				uni.navigateTo({url: '../tel/tel'});
			}
		},
		pickupBag(){
			if(uni.getStorageSync('opid')){
				uni.navigateTo({url: '../pickup/pickup'});
			}else{
				uni.navigateTo({url: '../tel/tel'});
			}
		}
	}
}
